const {ccclass, property} = cc._decorator;

export default class JoystickCommon extends cc.Component {
    @property(cc.Node)
    dot: cc.Node = null;

    @property( cc.Enum)
    JoystickType=cc.Enum({
        FOUR: 4,
        EIGHT: 8,
        ALL: 0,
    });

    DirectionType = cc.Enum({
        FOUR: 4,
        EIGHT: 8,
        ALL: 0,
    }); 

    SpeedType = cc.Enum({
        FOUR: 4,
        EIGHT: 8,
        ALL: 0,
    });
}
export { JoystickCommon }

  